<template>
  <v-dialog v-model="newGameDialog" max-width="290">
    <template v-slot:activator="{ on }">
      <div class="gameStart" v-ripple v-on="on">
        <div class="gameStart_label">Board Size</div>
        <div class="gameStart_value">{{settings.size ** 2}}</div>
        <div class="gameStart_label">Players</div>
        <div class="gameStart_value">{{settings.players}}</div>
        <div class="gameStart_label">Difficulty</div>
        <div class="gameStart_value">{{settings.difficulty === 0 ? 'Normal' : 'Hard'}}</div>
      </div>
    </template>
    <GameDialog v-on:close="newGameDialog = false" />
  </v-dialog>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameDialog from './GameDialog.vue'

export default {
  name: 'GameStart',
  components: {
    GameDialog
  },
  computed: mapState({
    settings: state => state.game.settings
  }),
  data: () => ({
    gameService: new GameService(),
    newGameDialog: false
  }),
  methods: {
    newGame () {
      this.newGameDialog = false
      this.gameService.newGame()
    }
  }
}
</script>

