<template>
  <v-card class="gameDialog">
    <v-card-title class="headline">New Game</v-card-title>
    <v-card-text>
      <v-radio-group
        label="BoardSize"
        :value="game.settings.size"
        color="primary"
        class="gameDialog_radioGroup"
        @change="update('size', $event)"
      >
        <v-radio
          v-for="item in boardSizes"
          :key="item.value"
          :label="item.name"
          :value="item.value"
          class="gameDialog_radio"
          color="primary"
        ></v-radio>
      </v-radio-group>
      <v-slider
        label="Players"
        :value="game.settings.players"
        @change="update('players', $event)"
        min="2"
        max="8"
        color="primary"
        thumb-label="always"
        class="gameDialog_slider"
      ></v-slider>
      <v-radio-group
        label="Difficulty"
        :value="game.settings.difficulty"
        color="primary"
        class="gameDialog_radioGroup"
        @change="update('difficulty', $event)"
      >
        <v-radio
          v-for="item in difficulties"
          :key="item.value"
          :label="item.name"
          :value="item.value"
          class="gameDialog_radio"
          color="primary"
        ></v-radio>
      </v-radio-group>
      <legend class="v-label theme--dark">Color</legend>
      <div class="gameDialog_colors">
        <div
          v-for="(color, i) in 8"
          class="gameDialog_color"
          :class="{'gameDialog_color-active' : +i === +game.settings.color}"
          @click="update('color', i)"
          :key="i"
          v-ripple
        >
          <div class="gameDialog_color_color" :class="['gameDialog_color-' + i]"></div>
        </div>
      </div>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn large text @click="close">Cancel</v-btn>
      <v-btn color="primary" large @click="newGame">Start</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import { boardSizes, difficulties } from '@/globals/globals'

export default {
  name: 'GameDialog',
  computed: mapState({
    settings: state => state.game.settings
  }),
  data: () => ({
    gameService: new GameService(),
    firstName: 'test',
    game: { settings: {} },
    boardSizes,
    difficulties
  }),
  mounted () {
    this.$set(this.game, 'settings', JSON.parse(JSON.stringify(this.settings)))
  },
  methods: {
    newGame () {
      this.changeSettings(this.game.settings)
      this.gameService.newGame()
      this.$emit('close', true)
    },
    close () {
      this.$emit('close', true)
    },
    update (key, val) {
      this.$set(this.game.settings, key, val)
    },
    ...mapActions('game', ['changeSettings'])
  }
}
</script>

<style lang="scss">
.gameDialog {
  &_radio {
    grid-column-start: 2;
    &Group {
      .v-input--radio-group__input {
        display: grid;
        grid-template-columns: 85px 1fr;
      }
    }
  }
  &_slider {
    margin-top: 30px;
    .v-label {
      width: 65px;
    }
    .v-slider__thumb {
      cursor: pointer;
    }
  }
  &_colors {
    display: flex;
    flex-wrap: wrap;
  }
  &_color {
    width: 40px;
    height: 40px;
    cursor: pointer;
    box-sizing: border-box;
    margin: 10px;
    position: relative !important;
    &:before {
      content: "";
      position: absolute;
      opacity: 0;
      top: 5px;
      right: -5px;
      bottom: -5px;
      left: 5px;
      background-color: rgba(255, 255, 255, 0.2);
      border-top-left-radius: 6px;
      border-bottom-right-radius: 6px;
      transition: opacity 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    }
    &_color {
      width: 40px;
      height: 40px;
      border-top-left-radius: 6px;
      border-bottom-right-radius: 6px;
      z-index: 0;
      position: relative;
    }
    &_hover {
      opacity: 0.8;
    }
    &-active {
      &:before {
        opacity: 1;
      }
    }
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: $color;
      }
    }
  }
}
</style>
