const state = {
  isMobile: false
}

// Getter functions
const getters = {
  getIsMobile () {
    return JSON.parse(JSON.stringify(state.isMobile))
  }
}

// Mutations
const mutations = {
  setIsMobile (state, data) {
    state.isMobile = data
  }
}

// Actions
const actions = {
  changeIsMobile ({ commit }, data) {
    commit('setIsMobile', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
